@import url(./bootstrap.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px; }
  body .App {
    text-align: center; }

.header-component {
  padding: 20px; }
  .header-component .title {
    display: inline-block;
    font-size: 5rem; }
    @media (max-width: 767px) {
      .header-component .title {
        margin: 0 auto; } }
  .header-component .logo {
    display: inline-block;
    margin-right: 20px;
    margin-top: -2%;
    -webkit-transition: all .25s ease;
    -o-transition: all .25s ease;
    transition: all .25s ease;
    -webkit-animation: App-logo-pulse infinite 10s ease both;
            animation: App-logo-pulse infinite 10s ease both;
    height: 80px; }
    .header-component .logo:hover {
      -webkit-animation: App-logo-pulse-hover infinite 10s ease both;
              animation: App-logo-pulse-hover infinite 10s ease both; }
    @media (max-width: 767px) {
