    margin: 20px auto; }
  .dates-component input {
    display: block;
    margin: 1px auto;
    color: black; }
  .dates-component .checkboxes label {
    float: left; }
  .dates-component .checkboxes ul {
    margin: 0;
    list-style: none;
    float: left; }

.holidays-component {
  margin-bottom: 40px; }

.calculator-component h2 {
  display: block;
  font-size: 4rem; }
  .calculator-component h2 span {
    font-size: 5rem; }
    .calculator-component h2 span.total-days {
      color: red; }
    .calculator-component h2 span.working-days {
      color: green; }

.calculator-component .display-days {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

@media (max-width: 767px) {
